.card {
    height: 100%;
}

.glass-blue {
    background: rgba(0, 93, 171, 0.75);
}

.glass-green {
    background: rgba(52, 155, 5, 0.75);
}

.glass-peach {
    background: rgb(243, 128, 81, 0.75);
}

.level-card {
    font-size: 1.5em;
    background: #F38051;
    margin-bottom: 2vh;
    -webkit-box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, .2);
            box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, .2);
}

.level-card > a {
    color: #fff;
}

.module1 {
    background: url(../../img/level1.png) no-repeat center;
    background-size: cover;
}

.module2 {
    background: url(../../img/level2.png) no-repeat center;
    background-size: cover;
}

.module3 {
    background: url(../../img/level3.png) no-repeat center;
    background-size: cover;
}

.module-bg {
    width: 100%;
    height: 100%;
}

.module-card {
    width: 100%;
    font-size: 1.5em;
    border: none;
    -ms-flex: 1 1;
        flex: 1 1;
}

.module-card > a {
    color: #fff;
    top: 40%;
    width: 100%;
    position: absolute;
    z-index: 3;
}

.module-glass {
    width: 100%;
    height: 100%;
    z-index: 2;
    position: absolute;
}


