html {
    height: 100%;
}

body {
    font-family: 'Raleway', sans-serif;
    height: 100%;
    background: rgba(0, 0, 0, 0.15);
}

#root {
    height: 100%;
}

a, a:hover {
    text-decoration: none
}

a > img {
    opacity: 0.7;
    filter: alpha(opacity=70); /* For IE8 and earlier */
}

a > img:hover {
    opacity: 1.0;
    filter: alpha(opacity=100); /* For IE8 and earlier */
}

.done-cup > img {
    max-width: 200px;
}

.done-page {
    height: 100%;
    background-color: #3498db;
    color: #fff;
}

.game-page {
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    z-index: -1;
}

.item-group {
    margin: 2vh 0;
    width: 100%;
    overflow: scroll;
}

.module-page {
    z-index: -1;
    padding-top: 2vh;
}

