.bg-level1 {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 93, 171, 0.75)), to(rgba(0, 93, 171, 0.75))), url("../../img/level1.png") no-repeat center;
    background: -webkit-linear-gradient(rgba(0, 93, 171, 0.75), rgba(0, 93, 171, 0.75)), url("../../img/level1.png") no-repeat center;
    background: -o-linear-gradient(rgba(0, 93, 171, 0.75), rgba(0, 93, 171, 0.75)), url("../../img/level1.png") no-repeat center;
    background: linear-gradient(rgba(0, 93, 171, 0.75), rgba(0, 93, 171, 0.75)), url("../../img/level1.png") no-repeat center;
    color: #fff;
    background-size: cover;
    min-height: 130px;
}

.bg-level1 > div > .btn {
    background-color: #349B52;
    color: white;
}

.bg-level1 > div > .btn:hover {
    color: #F38051;
}

.bg-level2 {
    background-image: url("../../img/level2.png");
}

.bg-level3 {
    background-image: url("../../img/level3.png");
}

.game-footer {
    z-index: 100;
    position: absolute;
    bottom: 3vh;
    width: 100%;
    max-width: 425px;
    /* height: 50px; */
}

.game-push {
    z-index: 1;
}

.game-wrap {
    width: 100%;
    height: 100%;
}

.game-wrap-child {
    z-index: -1;
    height: 100%;
    width: 100%;
}

.level-top {
    /* min-height: 160px; */
}

.level-top, .level-top > .col {
    margin: 0;
    padding: 0;
}

.match-item {
    border-radius: 0.5em;
    -webkit-box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, .2);
            box-shadow: 5px 5px 5px 0px rgba(0, 0, 0, .2);
}

.notification {
    width: 100%;
}

.qna {
    width: 100%;
    height: 100%;
    /* padding: 1em; */
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-direction: column;
        flex-direction: column;
}

.selected-answer {
    color: #fff;
    background-color: #F38051;
    border-radius: 0.5em;
    -webkit-box-shadow: 5px 5px 5px 0px rgba(243, 128, 81, .5);
            box-shadow: 5px 5px 5px 0px rgba(243, 128, 81, .5);
}

.selected-question {
    color: #fff;
    background-color: #005DAB;
    border-radius: 0.5em;
    -webkit-box-shadow: 5px 5px 5px 0px rgba(0, 93, 171, .5);
            box-shadow: 5px 5px 5px 0px rgba(0, 93, 171, .5);
}


